import { useState } from "react";
import { useDispatch } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { updateGeneralParam, updateStoredUser } from "../redux";

export default function Connect(props) {
    const [userObject, setUserObject] = useState({
        pseudo:'',
        email:'',
        password:'',
        is_admin: false
    })
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const handleChange = e => {
        const object = {...userObject};
        object[e.target.name] = e.target.value;
        setUserObject({...object});
    }

    //API fetch requete POST pour formulaire
    const connectSubmit = e => {
        e.preventDefault();
        const url = 'http://localhost:8000/login';
        /* const formData = new FormData();
        formData.append('user', JSON.stringify(data));
        formData.append('image', file); */

        let request = {
            method: 'POST',
            body: JSON.stringify(userObject),
            headers: {
            "Content-Type": "application/json"
            }
        };

        let status = '';

        fetch(url, request)
            .then(rep => {
                let response = rep.json();
                status = rep.status === 401 ? 'error' : 'logged';
                return response;
            })
            .then(value => {
                if (status === 'logged') {
                    const userLogged = {
                        pseudo:value.data.pseudo,
                        id: value.data._id,
                        is_admin: value.data.is_admin,
                        token: value.token
                    }
                    //console.log(value);
                    dispatch(updateGeneralParam({connected:true}));
                    sessionStorage.setItem('User',JSON.stringify(userLogged));
                    dispatch(updateStoredUser(userLogged));
                    props.showAlert(value.message,'valid');
                    setTimeout(() => {
                        navigate("/");
                    }, 2000);
                    return (userLogged);
                } else if (status === 'error') {
                    console.log('Erreur login');
                }
            })
            .catch(error => {
                console.log('erreur ! ' + error);
            })
    }
  return (
    <>
        <Link className="back" to="/"></Link>
        <form className="user-form">
            <div className='user-inputs' tabIndex='0'>
                <label htmlFor='email'>Email</label>
                <input type='text' name='email' max='50' onChange={handleChange} value={userObject.email} required />
            </div>
            <div className='user-inputs' tabIndex='0'>
                <label htmlFor='password'>Mot de passe</label>
                <input type='password' name='password' max='50' onChange={handleChange} value={userObject.password} required />
            </div>
            <div className="buttons-container">
                <button onClick={connectSubmit}>Se connecter</button>
            </div>
        </form>
    </>
  )
}
