import { configureStore, createSlice } from "@reduxjs/toolkit";

const generalParamsSlice = createSlice({
    name: "generalParams",
    initialState: {
            isLoading: false,
            connected: false,
            alertVisible: false,
            alertMessage: '',
            alertType: ''
        },
      reducers: {
        updateGeneralParam: (state, action) => {
            state = {...state,...action.payload};
            return state;
        }
      }
});
const userSlice = createSlice({
    name: "userObject",
    initialState: {
        pseudo:'',
        id:'',
        is_admin: false,
        token:''
    },
      reducers: {
        updateStoredUser: (state, action) => {
            const newUser = {...state,...action.payload};
            state = newUser;
            return state;
        },
        deleteStoredUser: state => {
            state = {
                pseudo:'',
                id:'',
                is_admin: false,
                token:''
            };
            return state;
        }
      }
});
const articlesSlice = createSlice({
    name: "articlesArray",
    initialState: [],
      reducers: {
        getAllArticles: (state, action) => {
            state = action.payload;
            return state;
        },
        updateStoredArticles: (state, action) => {
            const previousArray = [...state];
            const newArticle = action.payload;
            const previousArticleIndex = previousArray.findIndex(article => article._id === newArticle._id);
            (previousArticleIndex === -1 || !previousArray.length) ? previousArray.push(newArticle) : previousArray.splice(previousArticleIndex,1,newArticle);
            state = previousArray;
            return state;
        },
        deleteStoredArticle: (state,action) => {
            const previousArray = [...state];
            const id = action.payload;
            const previousArticleIndex = previousArray.findIndex(article => article._id === id)
            previousArticleIndex !== -1 && previousArray.splice(previousArticleIndex,1);
            state = previousArray;
            return state;
        }
      }
});

export const { updateGeneralParam } = generalParamsSlice.actions;
export const { updateStoredUser } = userSlice.actions;
export const { deleteStoredUser } = userSlice.actions;
export const { getAllArticles } = articlesSlice.actions;
export const { updateStoredArticles } = articlesSlice.actions;
export const { deleteStoredArticle } = articlesSlice.actions;

export const store = configureStore({
    reducer: {
        generalParams: generalParamsSlice.reducer,
        userObject: userSlice.reducer,
        articlesArray: articlesSlice.reducer
    }
})