import { createRef, useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import ConnectionFields from "../Composants/ConnectionFields";
import { updateStoredArticles } from "../redux";
import { v4 as uuidv4 } from 'uuid';
import formatDate from "../Composants/formatDate";
import { current } from "@reduxjs/toolkit";

export default function MainArticlesPage(props) {
    const articlesArray = useSelector(state => state.articlesArray);
    const userObject = useSelector(state => state.userObject);
    const connected = useSelector(state => state.generalParams.connected);

    const dispatch = useDispatch();

    const containerRef = useRef();

    //const checkScroll = e => setShowArrow((e.target.scrollTop > 400 && !showArrow) ? true : false);
    //const checkScroll = e => e.target.scrollTop > 400 && console.log(e.target.scrollTop);

    /* useEffect(() => {
        containerRef.current.addEventListener('scroll',checkScroll);
    }, []); */
  
  const observer = new IntersectionObserver(entries => {
        entries.map(e => {
            e.isIntersecting ? e.target.classList.remove('hide') : e.target.classList.add('hide');       
        })
    });    

    /* const checkScroll = e => setShowArrow(window.scrollY > 320 ? true : false);

    document.addEventListener('scroll', checkScroll); */

    const sendComment = (e,comment,index) => {
        e.preventDefault();
        const previousComments = [...articlesArray[index].comments];
        previousComments.push({...comment,created_at:formatDate(new Date()),_id:uuidv4()});
        const updatedArticle = {...articlesArray[index],comments:previousComments};
        updateArticle(updatedArticle);
    }

    const deleteComment = (articleIndex,index) => {
        if (window.confirm('Voulez-vous vraiment effacer le commentaire ?')) {
            const article = articlesArray[articleIndex];
            const commentsArray = [...article.comments];
            commentsArray.splice(index,1);
            const updatedArticle = {...article,comments:commentsArray};
            updateArticle(updatedArticle);
        }
    }

    const updateArticle = article => {
        const token = JSON.parse(sessionStorage.getItem('User')).token;
        
        const updatedArticle = {...article};

        const url = 'http://localhost:8000/updateArticle';

        let request = {
            method: 'PUT',
            body: JSON.stringify(updatedArticle),
            headers: {
            "Content-Type": "application/json",
            'Authorization': 'Bearer ' + token
            }
        };
        fetch(url, request)
            .then(rep => {
                const disc = rep.json();
                return disc;
            })
            .then(data => {
                props.showAlert(data.message,'modify');
                dispatch(updateStoredArticles(updatedArticle));
            })
            .catch(error => {
                console.log('erreur ' + error);
            })
    }

    const Article = (props) => {
        const [visible, setVisible] = useState(false);
        const [newComment, setNewComment] = useState({
            author:userObject.pseudo,
            authorID:userObject.id,
            content:'',
            created_at: '',
            modified_at: ''
        });

        const articleRef = useRef();

        const comments = props.article.comments;

        useEffect(() => {
          if (articleRef.current && props.articlesIndex > 0) observer.observe(articleRef.current);
        }, [articleRef])
        

        const handleChange = e => {
            const tempObject = {...newComment};
            tempObject.content = e.target.value;
            setNewComment({...tempObject});
        }

        //props.articleIndex > 2 && observer.observe(refsArray[props.articleIndex]);

        const Comment = (props) => {
             const [modifiedComment, setModifiedComment] = useState('');

             const modifyComment = (e,index) => {
                e.preventDefault();
                const article = articlesArray[props.articleIndex];
                const commentsArray = [...comments];
                const datetime = formatDate(new Date());
                const tempObject = {...modifiedComment,modified_at:datetime};
                commentsArray.splice(index,1,tempObject);
                const updatedArticle = {...article,comments:commentsArray};
                updateArticle(updatedArticle);
                setModifiedComment('');
            }

            const handleModify = e => {
                const tempObject = {...modifiedComment};
                tempObject.content = e.target.value;
                setModifiedComment({...tempObject});
            }

             return (
                <div className="comment-container" >
                    <h5>{props.comment.author}</h5>
                    {modifiedComment ? <form>
                        <input type="text" name="modified-comment" id="modified-comment" onChange={handleModify} value={modifiedComment.content} />
                        <button onClick={e => modifyComment(e,props.commentIndex)}>Publier</button>
                        <button onClick={() => setModifiedComment('')}>Annuler</button>
                    </form> : <p>{props.comment.content}</p>}
                    {(userObject.is_admin || userObject.id === props.comment.authorID) && !modifiedComment ? 
                    <div className="modify-delete">
                        <div className="modif-button" onClick={() => setModifiedComment({...props.comment})}></div>
                        <div className="delete-button" onClick={() => deleteComment(props.articleIndex,props.commentIndex)}></div>
                    </div> : null}
                </div>
             )
        }
        const articleClass = `article-container ${props.articlesIndex > 0 ? (props.articlesIndex%2 !== 0 ? "hide left" : "hide right") : ""}`;
        /* const articleClass = props.articlesIndex > 0 ? "article-container hide" : "article-container"; */

        return (
            <div className={articleClass} ref={articleRef}>
                <h4>{props.article.title}</h4>
                <div className={visible ? "paragraph visible" : "paragraph"}>
                    <p>{props.article.content}</p>
                    <div>
                        {comments.map((comment,index) => {
                            return <Comment key={uuidv4()} comment={comment} commentIndex={index} articleIndex={props.articlesIndex} />
                        })}
                    </div>
                </div>
                <div className="blank">
                    {connected && <form>
                        <label htmlFor="new-comment">Commentez</label>
                        <input type="text" name="new-comment" id="new-comment" onChange={handleChange} value={newComment.content} />
                        <button onClick={e => sendComment(e,newComment,props.articlesIndex)}>Publier</button>
                    </form>}
                    <p onClick={() => setVisible(!visible)}>{!visible ? "lire plus..." : "fermer"}</p>
                </div>
            </div>
        )
    }
    
  return (
    <>
        {/* {showArrow && <div className="arrow-up" onClick={e => window.scrollTo(0,0)}></div>} */}
        <Link className="back" to="/"></Link>
        <main className="articles-page">
            <ConnectionFields showAlert={props.showAlert} />
            <h1>Le Spectre Noir</h1>
            <section className="articles-container" ref={containerRef}>
                {articlesArray.map((article,index) => {
                    return (
                        <Article key={uuidv4()} article={article} articlesIndex={index} />
                    )
                })}
            </section>
        </main>
    </>
  )
}
