import bcrypt from 'bcryptjs';
import { useState } from "react";
import { useDispatch } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import formatDate from "../Composants/formatDate";
import generateTokenFunc from '../Composants/generateToken';
import { updateGeneralParam, updateStoredUser } from "../redux";

export default function Subscribe(props) {
    const [userObject, setUserObject] = useState({
        pseudo:'',
        email:'',
        password:'',
        confirm:'',
        is_admin: false
    })
    const [passwordLevel, setPasswordLevel] = useState(0);

    const dispatch = useDispatch();
    const navigate = useNavigate();

    const handleChange = e => {
        const object = {...userObject};
        const name = e.target.name;
        const value = e.target.value;
        object[name] = value;
        let valid = regexpCheck(name,value);
        e.target.parentElement.className = 'user-inputs';
        if (name === 'confirm' && value === userObject.password) {valid = true};
        valid ? e.target.parentElement.classList.add('valid') : e.target.parentElement.classList.add('invalid');
        if (value === '') {e.target.parentElement.className = 'user-inputs'};
        name === 'password' && testPassword(value);
        setUserObject({...object});
    }
    
    const regexpCheck = (name,value) => {
        let regExp = '';
        switch (name) {
            case 'pseudo':
                regExp = new RegExp(/^\w{3,50}$/);
                break;
            case 'email':
                regExp = new RegExp(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})$/);
                break;
            case 'password':
                regExp = new RegExp(/^(?=.*[0-9])(?=.*[a-z--])(?=.*[A-Z--])(?=.*[^0-9a-z--A-Z-- ]).{6,60}$/);
                break;
            default:
                break;
        }
        const valid = regExp && regExp.test(value) ? true : false;
        return valid;
    }

    const testPassword = password => {
        const totalNumbers = password.length - password.replace(/[0-9]/g,'').length;
        const totalMaj = password.length - password.replace(/[A-Z]/g,'').length;
        const totalSpecials = password.length - password.replace(/[^0-9a-z--A-Z--]/g,'').length;
        if (password.length > 30 && totalNumbers > 4 && totalMaj > 4 && totalSpecials > 4) {
        setPasswordLevel(3);
        } else if (password.length > 16 && totalNumbers > 2 && totalMaj > 2 && totalSpecials > 2) {
        setPasswordLevel(2);
        } else if (password.length > 8 && totalNumbers > 0 && totalMaj > 0 && totalSpecials > 0) {
            setPasswordLevel(1);
        } else {
            setPasswordLevel(0);
        }
    }

    const subscribeSubmit = e => {
        e.preventDefault();
        let validFields = false;
        Array.from(document.querySelectorAll('.user-inputs')).every(el => el.className === 'user-inputs valid') && (validFields = true);
        validFields ? subscribeFetch() : props.showAlert('Les champs ne sont pas correctement remplis','error');
    }

    //API fetch requete POST pour formulaire
    const subscribeFetch = async () => {
        const url = 'https://eu-west-2.aws.data.mongodb-api.com/app/data-imvta/endpoint/subscribe';
        const datetime = formatDate(new Date());
        /* const formData = new FormData();
        formData.append('user', JSON.stringify(data));
        formData.append('image', file); */
        const token = generateTokenFunc();

        const password = bcrypt.hashSync(userObject.password, 10);
        const tempObject = {...userObject,password,created_at:datetime,token};
        delete tempObject.confirm;


        let request = {
            method: 'POST',
            body: JSON.stringify(tempObject),
            headers: {
            "Content-Type": "application/json"
            }
        };
        
        try {
            const response = await fetch(url, request);
            if (!response.ok) {
                throw new Error(`Erreur HTTP : ${response.status}`)
            }
            const json = await response.json();
            if (json.data !== {}) {
                const userLogged = {...json.data};
                dispatch(updateGeneralParam({connected:true}));
                sessionStorage.setItem('User',JSON.stringify(userLogged));
                dispatch(updateStoredUser(userLogged));
                props.showAlert(json.message,'valid');
                sessionStorage.setItem('token',JSON.stringify({token,created_at:datetime}));
                /*setTimeout(() => {
                    navigate("/");
                }, 2000); */
            } else {
                props.showAlert(json.message,'error');
            }
        } catch (error) {
            console.log(error);
            props.showAlert(`Une erreur s'est produite : erreur ${error.status}`,'error');
        }

        /* fetch(url, request)
            .then(rep => {
                console.log(rep);
                let userProfil = rep.json();
                status = (rep.status === 401 || rep.status === 400 || rep.status === 500) ? 'error' : 'logged';
                return userProfil;
            })
            .then(value => {
                console.log(value);
                if (status === 'logged') {  
                    const userLogged = {
                        pseudo:value.data.pseudo,
                        id: value.data._id,
                        email: value.data.email,
                        token: value.token,
                        is_admin: false
                    }
                    //console.log(value);
                    dispatch(updateGeneralParam({connected:true}));
                    sessionStorage.setItem('User',JSON.stringify(userLogged));
                    dispatch(updateStoredUser(userLogged));
                    props.showAlert(value.message,'valid');
                    setTimeout(() => {
                        navigate("/");
                    }, 2000);
                    return (userLogged);
                } else if (status === 'error') {
                    console.log(value);
                }
            })
            .catch(error => {
                console.log('erreur ' + error);
            }) */
    }
    /* const subscribeFetch = () => {
        const url = 'http://localhost:8000/subscribe';
        const datetime = formatDate(new Date());
        const formData = new FormData();
        formData.append('user', JSON.stringify(data));
        formData.append('image', file);
        const tempObject = {...userObject};
        delete tempObject.confirm;
        tempObject.created_at = datetime;

        let request = {
            method: 'POST',
            body: JSON.stringify(tempObject),
            headers: {
            "Content-Type": "application/json"
            }
        };
        
        let status = '';

        fetch(url, request)
            .then(rep => {
                let userProfil = rep.json();
                status = (rep.status === 401 || rep.status === 400 || rep.status === 500) ? 'error' : 'logged';
                return userProfil;
            })
            .then(value => {
                if (status === 'logged') {  
                    const userLogged = {
                        pseudo:value.data.pseudo,
                        id: value.data._id,
                        email: value.data.email,
                        token: value.token,
                        is_admin: false
                    }
                    //console.log(value);
                    dispatch(updateGeneralParam({connected:true}));
                    sessionStorage.setItem('User',JSON.stringify(userLogged));
                    dispatch(updateStoredUser(userLogged));
                    props.showAlert(value.message,'valid');
                    setTimeout(() => {
                        navigate("/");
                    }, 2000);
                    return (userLogged);
                } else if (status === 'error') {
                    console.log(value);
                }
            })
            .catch(error => {
                console.log('erreur ' + error);
            })
    } */
    
  return (
    <main className="subscribe-page">
        <Link className="back" to="/"></Link>
        {userObject.password && document.querySelectorAll('.user-inputs')[2].className !== 'user-inputs valid' ? <div className="indice">
            <p>Le mot de passe doit contenir au moins une majuscule, un chiffre et un caractre spcial</p>
        </div> : null}
        <form className="user-form">
            <div className='user-inputs' tabIndex='0'>
                <label htmlFor='pseudo'>Pseudo</label>
                <input type='text' name='pseudo' max='30' onChange={handleChange} value={userObject.pseudo} required />
                <div className="validity"></div>
            </div>
            <div className='user-inputs' tabIndex='0'>
                <label htmlFor='email'>Email</label>
                <input type='text' name='email' max='50' onChange={handleChange} value={userObject.email} required />
                <div className="validity"></div>
            </div>
            <div className='user-inputs' tabIndex='0'>
                <label htmlFor='password'>Mot de passe</label>
                <input type='password' name='password' max='40' onChange={handleChange} value={userObject.password} required />
                <div className="validity"></div>
            </div>
            <div className='user-inputs' tabIndex='0'>
                <label htmlFor='confirm'>Confirmez le mot de passe</label>
                <input type='password' name='confirm' max='40' onChange={handleChange} value={userObject.confirm} required />
                <div className="validity"></div>
            </div>
            {<div className="password-force">
                <p>Force du mot de passe</p>
                <div className="lights-container">
                    <div className={passwordLevel === 1 ? "low" : ""}></div>
                    <div className={passwordLevel === 2 ? "mid" : ""}></div>
                    <div className={passwordLevel === 3 ? "high" : ""}></div>
                </div>
            </div>}
            <div className="buttons-container">
                <button onClick={subscribeSubmit}>Crer</button>
            </div>
        </form>
    </main>
  )
}
