import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom'
import { deleteStoredUser, updateGeneralParam } from '../redux';

export default function ConnectionFields(props) {
    const connected = useSelector(state => state.generalParams.connected);
    const userObject = useSelector(state => state.userObject);
    const dispatch = useDispatch();

    const disconnectFunc = () => {
        dispatch(updateGeneralParam({connected:false}));
        dispatch(deleteStoredUser());
        props.showAlert('Vous tes dconnect','valid');
    }

  return (
    <nav className="connection-container">
        {connected ? 
        <>  
            {userObject.is_admin && 
            <Link to="/createArticle">
                <button>Ecrire un article</button>
            </Link>}
            <Link to="/">
                <button onClick={disconnectFunc}>Dconnexion</button> 
            </Link>
        </> : 
        <>
            <Link to="/subscribe">
                <button>inscription</button>
            </Link>
            <Link to="/connect">
                <button>connexion</button>
            </Link>
        </>}
    </nav>
  )
}
