import { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import ConnectionFields from "../Composants/ConnectionFields";
import { getAllArticles } from "../redux";

export default function Home(props) {
    const articlesArray = useSelector(state => state.articlesArray);
    
    const [arrowVisible, setArrowVisible] = useState(true);
    const [showPresentation, setShowPresentation] = useState(false);

    const checkScroll = e => setArrowVisible(window.scrollY > 130 ? false : true);

  document.addEventListener('scroll', checkScroll);

  const observerArticle = useRef();

  const observer = new IntersectionObserver(entries => {
        entries.map(e => {
            setShowPresentation(e.isIntersecting ? true : false);
        })
    });

    const dispatch = useDispatch();

    const getArticles = async () => {
        const url = `https://eu-west-2.aws.data.mongodb-api.com/app/data-imvta/endpoint/displayArticles`;
        let request = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Request-Headers': '*'
            }
        };
        try {
          const response = await fetch(url,request);
          if (!response.ok) {
              throw new Error(`Erreur HTTP : ${response.status}`);
          }
          const arrayResults = await response.json();
          //dispatch(updateGeneralParam({isLoading:false}));
          dispatch(getAllArticles(arrayResults));
            props.showAlert('Articles chargs','valid');
        }
        catch(error) {
          console.error(error);
          //dispatch(updateGeneralParam({isLoading:false}));
      }
    }

    useEffect(() => {
        observer.observe(observerArticle.current);
        !articlesArray.length && getArticles();
    }, [])
    

  return (
    <main className="home-page">
        <ConnectionFields showAlert={props.showAlert} />
        {arrowVisible && <div className="more-arrow"></div>}
        <div className="title-container">
            <h1>Le Spectre Noir</h1>
        </div>
        <section>
            <div className={showPresentation ? "presentation appears" : "presentation"} ref= {observerArticle}>
                <h2>Le projet</h2>
                <p>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Quaerat nisi corporis consequuntur alias quos libero itaque nulla sed eligendi laudantium aliquam quidem animi ab omnis eius deserunt dicta nostrum repellat veritatis, iste dolore doloribus culpa. Nulla voluptatum id voluptatem delectus labore rerum repudiandae totam recusandae tenetur, nemo, magnam eius quam. Laborum vel modi saepe eaque, illum officia voluptate doloribus ullam eum et? Repellat architecto dolorem id, assumenda autem porro. Temporibus eaque recusandae esse perferendis possimus laudantium dolores! Repudiandae rerum asperiores hic. Vitae nemo obcaecati minus similique et consequatur vel! Excepturi consectetur reprehenderit inventore iste magni voluptate ea libero! Fuga quasi sit distinctio accusantium pariatur ducimus nobis cupiditate soluta, expedita, sapiente excepturi veniam quae? Mollitia nulla deserunt quia doloribus, commodi ab vel porro dolor qui hic consequuntur adipisci tempore, debitis quibusdam modi impedit, molestias ipsum. Similique quaerat natus consectetur quisquam molestiae ea non voluptate sit distinctio, minus praesentium placeat asperiores enim!
                </p>
            </div>
        </section>
        <footer className={showPresentation ? "footer appears" : "footer"}>
            <Link to="/articles">
                <h3>Les articles</h3>
            </Link>
        </footer>
    </main>
  )
}
