import { useState } from "react"
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import formatDate from "../Composants/formatDate";
import { deleteStoredArticle, updateStoredArticles } from "../redux";

export default function CreateModifyArticle(props) {
    const [newArticle, setNewArticle] = useState({
        title:'',
        content:'',
        comments: [],
        created_at: '',
        modified_at: ''
    });

    const dispatch = useDispatch();

    const handleChange = e => {
        const tempObject = {...newArticle};
        tempObject[e.target.name] = e.target.value;
        setNewArticle({...tempObject});
    }

    const createArticle = e => {
        e.preventDefault();
        const token = JSON.parse(sessionStorage.getItem('User')).token;
        const datetime = formatDate(new Date());
        //API fetch requete POST pour formulaire
        const url = 'http://localhost:8000/newArticle';
        let request = {
            method: 'POST',
            body: JSON.stringify({...newArticle,created_at:datetime}),
            headers: {
            "Content-Type": "application/json",
            'Authorization': 'Bearer ' + token
            }
        };
        fetch(url, request)
            .then(rep => {
                let article = rep.json();
                return article;
            })
            .then(value => {
                setNewArticle({...value.data});
                dispatch(updateStoredArticles(value.data));
                props.showAlert(value.message,'valid');
            })
            .catch(error => {
                console.log('erreur ' + error);
            })
    }

    const updateArticle = e => {
        e.preventDefault();
        const token = JSON.parse(sessionStorage.getItem('User')).token;
        const datetime = formatDate(new Date());
        
        const updatedArticle = {...newArticle,modified_at:datetime};

        const url = 'http://localhost:8000/updateArticle';

        let request = {
            method: 'PUT',
            body: JSON.stringify(updatedArticle),
            headers: {
            "Content-Type": "application/json",
            'Authorization': 'Bearer ' + token
            }
        };
        fetch(url, request)
            .then(rep => {
                const disc = rep.json();
                return disc;
            })
            .then(value => {
                props.showAlert(value.message,'modify');
                dispatch(updateStoredArticles(updatedArticle));
            })
            .catch(error => {
                console.log('erreur ' + error);
            })
    }

    const deleteArticle = e => {
        e.preventDefault();
        const token = JSON.parse(sessionStorage.getItem('User')).token;

        const url = 'http://localhost:8000/deleteArticle';

        let request = {
            method: 'DELETE',
            body: JSON.stringify(newArticle),
            headers: {
            "Content-Type": "application/json",
            'Authorization': 'Bearer ' + token
            }
        };
        if (window.confirm('Voulez-vous effacer l\'article ?')) {
            fetch(url, request)
               .then(rep => {
                   const disc = rep.json();
                   return disc;
               })
               .then(data => {
                   //dispatch(updateGeneralParam({isLoading:false}));
                   props.showAlert(data.message,'valid');
                   setNewArticle({
                       title:'',
                       content:'',
                       id:'',
                        comments: [],
                        created_at: '',
                        modified_at: ''
                   });
                   dispatch(deleteStoredArticle(newArticle._id));
               })
               .catch(error => {
                   //dispatch(updateGeneralParam({isLoading:false}));
                   console.log('erreur ' + error);
               })
        }
    }

  return (
    <>
        <Link className="back" to="/"></Link>
        <main className="create-aticle-page">
            <h1>Nouvel article</h1>
            <form>
                <div className='article-inputs' tabIndex='0'>
                    <label htmlFor='title'>Titre</label>
                    <input type='text' name='title' max='50' onChange={handleChange} value={newArticle.title} required />
                </div>
                <div className='article-inputs' tabIndex='0'>
                    <label htmlFor='content'>Article</label>
                    <textarea type='text' name='content' max='50' onChange={handleChange} value={newArticle.content} required />
                </div>
                {newArticle._id ? <div className="buttons-container">
                    <button onClick={updateArticle}>Modifier</button>
                    <button onClick={deleteArticle}>Effacer</button>
                </div> : 
                <div className="buttons-container">
                    <button onClick={createArticle}>Crer</button>
                </div>}
            </form>
        </main>
    </>
  )
}
