
const generateRandomNumbers = lettersNumbersString => {
    let codeArray = new Uint32Array(120);
    crypto.getRandomValues(codeArray);
    codeArray = codeArray.map(e => {
        const newNumber = e / 4294967296;
        const index = Math.floor(newNumber * lettersNumbersString.length);
        return index;
    })
    return codeArray;
  }

  const getCharacters = (first,last) => {
    const forbiddenCaracters = [34,39,40,41,46,47,59,60,62,63,92];
    let string ='';
    for (let i = first; i <= last; i++) {
        !forbiddenCaracters.includes(i) && (string += (String.fromCharCode(i)));
    }
    return string;
  }

  const concatenateCaracters = (array,string) => {
    let token = '';
    array.map(e => {
        token += string[e];
    })
    return token;
}

  export default function generateTokenFunc() {
    const lowercaseLetters = getCharacters(97,122);
    const uppercaseLetters = getCharacters(65,90);
    const allIntegers = getCharacters(48,57);
    //const specialsCaracters = getCharacters(33,47)+getCharacters(58,64)+getCharacters(91,95)+getCharacters(123,126);
    const lettersNumbersString = lowercaseLetters + uppercaseLetters + allIntegers;
    const array = generateRandomNumbers(lettersNumbersString);
    const newToken =concatenateCaracters(array,lettersNumbersString);
    return newToken;
  }