import { useDispatch, useSelector } from "react-redux";
import { Route, Routes } from "react-router-dom";
import Connect from "./components/Connect";
import CreateModifyArticle from "./components/CreateModifyArticle";
import Home from "./components/Home";
import MainArticlesPage from "./components/MainArticlesPage";
import Subscribe from "./components/Subscribe";
import { updateGeneralParam } from "./redux";

function App() {
  const generalParams = useSelector(state => state.generalParams);

  const dispatch = useDispatch();

  const showAlert = (message,type) => {
    const alertType = type ? type : '';
    dispatch(updateGeneralParam({alertMessage:message,alertVisible:true,alertType}));
    setTimeout(() => {
      dispatch(updateGeneralParam({alertVisible:false}));
    }, 2000);
  };

  return (
    <div className="App">
      {generalParams.alertVisible && <div className="alert-window">
        <div className="alert-message-container">
          <div className={generalParams.alertType}></div>
          <p>{generalParams.alertMessage}</p>
        </div>
      </div>}
      <Routes>
        <Route path="/" element={<Home showAlert={showAlert}/>} />
        <Route path="/connect" element={<Connect showAlert={showAlert}/>} />
        <Route path="/subscribe" element={<Subscribe showAlert={showAlert}/>} />
        <Route path="/createArticle" element={<CreateModifyArticle showAlert={showAlert}/>} />
        <Route path="/articles" element={<MainArticlesPage showAlert={showAlert}/>} />
      </Routes>
    </div>
  );
}

export default App;